body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  display: flex;
  height: 300px;
  width: 300px;
  flex-basis: 100%;
  font-size: 100px;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
}

.block-1 {
  background-color: red;
}

.block-2 {
  background-color: rgb(204, 0, 0);
}

.block-3 {
  background-color: rgb(153, 0, 0);
}

.block-4 {
  background-color: rgb(102, 0, 0);
}

.block-5 {
  background-color: rgb(51, 0, 0);
}

.block-6 {
  background-color: rgb(0, 0, 0);
}

@media(min-width:600px) {
  .block {
    flex-basis: 50%;
  }

  .block-3 {
    order: 2;
  }

  .block-4 {
    order: 1;
  }

  .block-5 {
    order: 3;
  }

  .block-6 {
    order: 4;
  }
}

@media(min-width:900px) {
  .block {
    flex-basis: calc(100% / 3);
  }

  .block-3 {
    order: 0;
  }

  .block-4 {
    order: 3;
  }

  .block-5 {
    order: 2;
  }

  .block-6 {
    order: 1;
  }
}
